<template>
  <div class="detail">
    <div class="top box">
      <div class="row">
        <div class="left">
          站点信息:
        </div>
        <div class="right">
          {{changeApplicctionData.line}} {{changeApplicctionData.station}}
        </div>
      </div>
      <div class="hr"></div>
      <div class="row">
        <div class="left">
          个人班次:
        </div>
        <div class="right">
          <span v-if="isOrigin">
            {{changeApplicctionData.originDate | moment('YYYY-MM-DD')}} {{changeApplicctionData.originDON}} ({{changeApplicctionData.originTime}})
          </span>
          <span v-else>
            {{changeApplicctionData.changeDate | moment('YYYY-MM-DD')}} {{changeApplicctionData.changeDON}} ({{changeApplicctionData.changeTime}})
          </span>
        </div>
      </div>
      <div class="hr"></div>
      <div class="row">
        <div class="left">
          换班对象:
        </div>
        <div class="right">
          <span v-if="isOrigin">
            {{changeApplicctionData.originUser}}({{changeApplicctionData.originUserJobNum}})
          </span>
          <span v-else>
            {{changeApplicctionData.changeUser}}({{changeApplicctionData.changeUserJobNum}})
          </span>
        </div>
      </div>
      <div class="hr"></div>
      <div class="row">
        <div class="left">
          换班班次:
        </div>
        <div class="right">
          <span v-if="isOrigin">
            {{changeApplicctionData.changeDate | moment('YYYY-MM-DD')}} {{changeApplicctionData.changeDON}} ({{changeApplicctionData.changeTime}})
          </span>
          <span v-else>
            {{changeApplicctionData.originDate | moment('YYYY-MM-DD')}} {{changeApplicctionData.originDON}} ({{changeApplicctionData.originTime}})
          </span>
        </div>
      </div>
    </div>
    <div class="middle box">
      <div class="row">
        换班原因:
      </div>
      <div class="hr"></div>
      <div class="row">
        {{changeApplicctionData.changeReason}}
      </div>
    </div>
    <div class="bottom box">
      <div class="row">
       状态:
      </div>
      <div class="hr"></div>
      <div class="line_box">
        <steps class="step_line" :line-data="lineData"/>
      </div>
    </div>
    <div class="button_box" v-if="!isOrigin&&changeApplicctionData.status==='待确认'">
      <button @click="commitHandle('同意')">同意</button>
      <div class="black_div"></div>
      <button class="gray" @click="commitHandle('拒绝')">拒绝</button>
    </div>
    <div class="blank"></div>
  </div>
</template>

<script>
import Steps from './components/Steps'
export default {
  name: 'CDetail',
  components: {
    Steps
  },
  data () {
    return {
      id: '',
      jobNum: 'Q001234',
      changeApplicctionData: {
        id: 1,
        line: '一号线',
        station: '后卫寨',
        originDate: '2018-10-11',
        originDON: '夜',
        originTime: '18:00-06:00',
        originUser: '李四',
        originUserJobNum: 'Q003333',
        changeDate: '2018-10-10',
        changeDON: '白',
        changeTime: '09:00-18:00',
        changeUser: 'zhangsna',
        changeUserJobNum: 'Q001234',
        changeReason: '家里有急事，需要进行换班，希望能够同意',
        passUser: '王五',
        sendTime: '2018-03-15 22:00',
        comfirmTime: '2018-03-15 22:00',
        passTime: '2018-03-15 22:00',
        status: '已通过'
      }
    }
  },
  computed: {
    isOrigin () {
      return this.changeApplicctionData.originUserJobNum === this.jobNum
    },
    lineData () {
      return {
        originUser: this.changeApplicctionData.originUser,
        sendTime: this.changeApplicctionData.sendTime,
        changeUser: this.changeApplicctionData.changeUser,
        comfirmTime: this.changeApplicctionData.comfirmTime,
        passUser: this.changeApplicctionData.passUser,
        passTime: this.changeApplicctionData.passTime,
        status: this.changeApplicctionData.status
      }
    }
  },
  methods: {
    getShiftsDataDetail (id) {
      this.$rpc.call('funenc_xa_station.change_shifts', 'get_change_shifts_by_id', [], {
        id: id
      }).then(res => {
        this.changeApplicctionData = res
      }).catch(e => {
        console.log(e)
      })
    },
    commitHandle(state){
      this.$rpc.call('funenc_xa_station.change_shifts', 'save_state', [], {
        id: this.id,
        states: state
      }).then(res => {
        console.log(res)
        if(res) {
          this.getShiftsDataDetail(this.id)
        }
      }).catch(e => {
        console.log(e)
      })
    }
  },
  mounted () {
    this.id = this.$route.params.id
    let userInfo = JSON.parse(sessionStorage.getItem('userInfo'))
    this.jobNum = userInfo && userInfo.jobnumber
    this.getShiftsDataDetail(this.id)
  }
}
</script>

