<template>
  <div class="step">
    <ul>
      <li>
        <div class="l pass"></div>
        <div class="r">
          <div class="h">
            发起
          </div>
          <div class="c">
            {{lineData.originUser}} {{lineData.sendTime}}
          </div>
        </div>
      </li>
      <li>
        <div class="l" :class="{pass: nowStep > 1}"></div>
        <div class="r">
          <div class="h" :class="(nowStep<2.6)?_statusColor(lineData.status):'tongguo'" v-text="(nowStep<2.6)?lineData.status:'已通过'">
          </div>
          <div class="c" :class="(nowStep<2.6)?_statusColor(lineData.status):'tongguo'">
            {{lineData.changeUser}} {{lineData.comfirmTime || '无'}}
          </div>
        </div>
      </li>
      <li>
        <div class="l" :class="{pass: nowStep > 2.5}"></div>
        <div class="r" v-if="nowStep > 2.5">
          <div class="h" :class="_statusColor(lineData.status)">
            {{lineData.status}}
          </div>
          <div class="c" :class="_statusColor(lineData.status)">
            {{lineData.passUser || '无'}} {{lineData.passTime || '无'}}
          </div>
        </div>
      </li>
    </ul>
    <div class="time_bar"></div>
    <div class="time_bar_green" :style="greenBar"></div>
  </div>
</template>

<script>
export default {
  name: 'Steps',
  props: {
    lineData: {
      type: Object,
      default () {
        return {
          sponsorsUser: '李四',
          startTime: '2018-03-15 22:00',
          comfirmUser: '张三',
          comfirmTime: '2018-03-15 22:00',
          passUser: 'wangwu',
          passTime: '2018-03-15 22:00',
          status: '已通过'
        }
      }
    }
  },
  computed: {
    nowStep () {
      if (this.lineData.status === '待确认') {
        return 1
      } else if (this.lineData.status === '已拒绝') {
        return 2
      } else if (this.lineData.status === '待审批') {
        return 2.5
      } else {
        return 3
      }
    },
    greenBar () {
      if (this.nowStep < 2) {
        return {
          height: '25%'
        }
      } else if (this.nowStep < 2.5) {
        return {
          height: '50%'
        }
      } else if (this.nowStep < 3) {
        return {
          height: '70%'
        }
      } else {
        return {
          height: '80%'
        }
      }
    }
  },
  methods: {
    _statusColor (status) {
      switch (status) {
        case '待确认':
          return 'queren'
        case '待审批':
          return 'shenpi'
        case '已拒绝':
          return 'jujue'
        case '已驳回':
          return 'bohui'
        case '已通过':
          return 'tongguo'
        default:
          break
      }
    }
  }
}
</script>

